import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Layout, PageHeader, Form, message, Button, InputNumber, Input, Alert } from 'antd';
import Apis from '@/apis'
import { Selector, } from '@/components';
const { Content } = Layout;

@inject(({globalStore})=>({
  getSetting: globalStore.getSetting,
  setting: globalStore.setting,
  permissions: globalStore.permissions,
  formItemLayout: globalStore.formItemLayout,
  formTailLayout: globalStore.formTailLayout,
}))
@observer
class EditView extends Component {
  formRef = React.createRef();
  state = {
    loading: true,
  };

  handleSubmit = values => {
    const { setting={}, getSetting } = this.props
    // if(values.currentReader) values.currentReader = values.currentReader.split(',')[0]
    Apis.settingUpdate({
      _id: setting._id, ...values,
    }).then(res => {
      if(res){
        message.success('!')
        setTimeout(() => {
          getSetting()
        }, 1000);
      }else{
        message.error('!')
      }
    })
  };
  handleOpenReader = val => {
    // console.warn(val);
  }

  render() {
    const { formItemLayout, formTailLayout, setting={}, permissions } = this.props
    return (
      <Content>
        <PageHeader
          ghost={true}
          className="page-title"
          onBack={false}
          title={``}
        />
        {permissions['manageSetting/update'] ? 
        <Content>
          <div className="app-content">
         <Form {...formItemLayout} onFinish={this.handleSubmit} initialValues={setting} ref={this.formRef} className="app-form">
            <Form.Item label="IP" name="encryptionerIp">
              <Input />
            </Form.Item>
            <Form.Item label="" name="encryptionerPort">
              <Input />
            </Form.Item>
            <Form.Item label="" name="currentReader">
              <Selector 
                api="dictionaryOptions" 
                params={{_id: '60181a67f003ed1289377022'}}
                titlepropname="key"
                valuepropname="value"
              />
            </Form.Item>
            <Form.Item label="ClassId" name="ocxClassId">
              <Input />
            </Form.Item>
            {/* <Form.Item label="" name="openCloseReader" valuePropName="checked">
              <Switch checkedChildren="" unCheckedChildren="" onChange={this.handleOpenReader} />
            </Form.Item> */}
            <Form.Item label="(KB)" name="uploadMax">
              <InputNumber />
            </Form.Item>
            <Form.Item label="" name="pageSize" help="20">
              <InputNumber />
            </Form.Item>
            {
              permissions['manageSetting/update'] &&
            <Form.Item {...formTailLayout}>
              <Button type="primary" htmlType="submit" style={{marginRight: 10}}></Button>
            </Form.Item>
            }
          </Form>
          </div>
        </Content>
        : <Alert
          message=""
          description=", ."
          type="warning"
          showIcon
          style={{margin: '0 20px'}}
        />}
      </Content>
    )
  }
};

export default EditView;
